#workSection{
    height: 200vh;
}

#downloadSection, #connectingSection{
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.phonePic{
    height:100vh;
    width: 40%;
}

.textContainer>h1,p{
    width: 40%;
    margin-bottom: 20px;
}

.textContainer h1{
    font-size: 40px;
}

.textContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 10%;
    padding-bottom: 10%;
    align-items: center;
    width: 50%;
}

.whiteButton{
    background-color: white;
    height: 60px;
    width: 50%;
    border-radius: 20px;
    /* border-style: none; */
    border-color: black;
    line-height: 60px;
    text-align: center;
    
}

.fa-app-store, .fa-google-play{
    font-size: 25px;
    margin-right: 20px;
}


