.cardImg{
    width: 100%;
    
}

#bottomAbout{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.card{
    width: 25%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#aboutSection{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: white;
}

#topAbout{
    margin-top: 20px;
    width: 40%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#topAbout h1{
    font-size: 40px;
}

#topAbout p{
    color:rgb(160, 160, 160);
    margin-top: 30px;
}

hr{
    width: 100%;
    border: 1.5px solid lightgray;
}

.card > img,h2,p{
    margin-bottom: 20px;
}

.card p{
    color:rgb(160, 160, 160) ;
    width: 50%;
}