#contactSection{
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: space-evenly; */
    justify-content: center;
}

.submitButton{
    background-color: black;
    color: white;
    height: 60px;
    width: 20%;
    border-radius: 20px;
    /* border-style: none; */
    border-color: black;
    line-height: 60px;
    text-align: center;
    
}

.inputSubmitContainer{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#email{
    width: 500px;
    height: 50px;
    margin-right: 20px;
    border-radius: 20px;
    border-style: solid;
    border-color: black;
}